// 操作日志相关接口
import axios from './../fetch'

// 查询商家日志
export const storelogList = params => { return axios.get('/admin/store/storelog/page', { params: params }) }

// 删除商家日志
export const storelogGet = params => { return axios.get('/admin/store/storelog/get', { params: params }) }



