<template>
  <div class="cms-list-layout">
    <cms-prompt collapse='true'>
      <li>显示店铺所有的操作日志</li>
    </cms-prompt>
    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>操作用户</label>
        <el-input v-model="queryParams.username" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>IP</label>
        <el-input v-model="queryParams.clientIp" @keyup.native.enter="query"></el-input>
      </div>

      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column prop="uri" label="请求地址" min-width="200"></el-table-column>
      <el-table-column prop="clientIp" label="访问/请求IP" min-width="150"></el-table-column>
      <el-table-column prop="method" label="请求方式" min-width="150"></el-table-column>
      <el-table-column prop="username" label="操作用户" min-width="150"></el-table-column>
      <el-table-column prop="createTime" label="请求时间" min-width="150">
        <template slot-scope="scope">
          {{$moment(scope.row.infosTime)}}
        </template>
      </el-table-column>
      <el-table-column label="更多" width='175' fixed="right" align="center">
        <div slot-scope="scope">
          <el-popover
            placement="left-start"
            width="300"
            @show="showData(scope.row.id)"
            trigger="hover">
            <div style="padding:10px;font-size:12px;">
              <p>sessionId：{{logShow.sessionId}}</p>
              <p>状态码：{{logShow.httpStatusCode}}</p>
              <p>耗时：{{logShow.timeConsuming}}</p>
              <p>请求参数：{{logShow.paramData}}</p>
              <p>响应参数：{{logShow.returnData}}</p>
            </div>
            <cms-button icon='edit' size='small' type='info' slot="reference">查看</cms-button>
          </el-popover>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import list from '@/mixins/list'
import { storelogList, storelogGet } from '@/api/setting/log.js'
var defaultData = {
  username: '',
  clientIp: '',
  page: 1,
  size: 10
}
export default {
  name: 'storeLog',
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData),
      logData: {},
      logShow: {}
    }
  },
  methods: {
    getDetail(id) {
      if (this.logData[id]) {
        this.logShow = Object.assign([], this.logData[id])
      } else {
        storelogGet({ id: id }).then(res => {
          if (res.code === 200) {
            this.logShow = res.data
            this.logData[id] = res.data
          }
        })
      }
    },
    showData(id) {
      this.getDetail(id)
    }
  },
  created() {
    // 首次触发
    this.getTableData(storelogList, this.queryParams)
  }
}
</script>

<style>
</style>



